:root {
  --black: #1c232b;
  --grey: #6c7289;
  --cream: #f2eae2;
  --white: #fff;
  --green-500: #3d8168;
  --green-700: #1a4032;
  --ff-serif: "Fraunces", Georgia;
  --ff-sans: "Montserrat", system-ui;
  --font-weight-medium: 500;
  --font-weight-semi-bold: 600;
  --font-weight-bold: 700;
  --line-height-100: 1;
  --line-height-110: 1.1;
  --line-height-120: 1.2;
  --line-height-160: 1.6;
  --letter-spacing-0: 0;
  --letter-spacing-5: .5rem;
}

html {
  font-size: 10px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--cream);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.text-preset-1 {
  font-family: var(--ff-serif);
  font-size: 3.2rem;
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-100);
  letter-spacing: var(--letter-spacing-0);
}

.text-preset-2 {
  font-family: var(--ff-sans);
  font-size: 1.4rem;
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-110);
  letter-spacing: var(--letter-spacing-0);
}

.text-preset-3 {
  font-family: var(--ff-sans);
  font-size: 1.4rem;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-160);
  letter-spacing: var(--letter-spacing-0);
  color: var(--grey);
}

.text-preset-4 {
  font-family: var(--ff-sans);
  font-size: 1.2rem;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-120);
  letter-spacing: var(--letter-spacing-5);
  text-transform: uppercase;
  color: var(--grey);
}

.text-preset-5 {
  font-family: var(--ff-sans);
  font-size: 1.3rem;
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-120);
  letter-spacing: var(--letter-spacing-0);
}

.button {
  background-color: var(--green-500);
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: .8rem;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  padding: 1.6rem 3.2rem;
  display: flex;
}

.button img {
  width: 1.8rem;
  height: 1.8rem;
}

.bg-white {
  background-color: var(--white);
}

.card {
  border-radius: .8rem;
  width: 35rem;
}

.card picture img {
  border-top-left-radius: .8rem;
  border-top-right-radius: .8rem;
  width: 100%;
}

.card .content {
  flex-direction: column;
  gap: 2.4rem;
  padding: 3.2rem;
  display: flex;
}

.card .content .price {
  align-items: center;
  display: flex;
}

.card .content .price > data:first-child {
  color: var(--green-500);
  margin-right: 1.6rem;
}

.card .content .price s > data {
  color: var(--grey);
}

@media screen and (width >= 768px) {
  .card {
    flex-direction: row;
    width: 60rem;
    display: flex;
  }

  .card picture {
    flex: 1;
    display: flex;
  }

  .card picture img {
    border-top-right-radius: 0;
    border-bottom-left-radius: .8rem;
  }

  .card .content {
    flex: 1;
    gap: 2.4rem;
    padding: 4rem;
  }
}
